<template>
    <div id="">
        <form style="width: 100%;" @submit.prevent="submit">
            <CRow class="mb-3 ml-2">
                <h3>미납액 차감</h3>
            </CRow>
            <CRow style="width: 80%">
                <table class="table table-striped" style="margin-left: 3%; border-collapse: collapse">
                    <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>이름</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <CInput v-model="custItem.name" readonly=""/>
                        </td>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>진료번호</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <CInput v-model="custItem.registNo" readonly=""/>
                        </td>
                    </tr>
                    <tr>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>미납액</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <CInput v-model="custItem.charge" readonly=""/>
                        </td>
                    </tr>
                    <tr>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>납부구분</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <select v-model="selected" class="form-control">
                                <option value="2">오프라인결제</option>
                                <option value="3">금액조정</option>
                            </select>
                        </td>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>금액</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <div style="float: left; margin-right: 2%;">
                                -
                            </div>
                            <div style="float: left">
                                <CInput placeholder="0" v-model="amount"/>입력하신 금액만큼 미납액에서 차감됩니다.
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="col-title">
                            <span class="table-span-margin">
                                <strong>비고</strong>
                            </span>
                        </td>
                        <td style="width: 30%">
                            <CInput v-model="userInfo"/>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </CRow>
            <CRow style="margin-bottom: 3%;">
                <CCol class="btn-col-margin ml-4" sm="1">
                    <button label=""
                            type="button"
                            class="btn btn-info btn-block"
                            @click="submit">
                            차감하기</button>
                </CCol>
            </CRow>
        </form>
    </div>
</template>
<style>
    .btn-col-margin{
        margin-top: 5px;
        margin-left: 15px;
        margin-right: 10px;
    }
    .col-title{
        width: 20%;
    }
    .table-span-margin{
        margin-left: 10%;
    }
</style>
<script>
    import axios from "axios"

    export default {
        name: 'app',
        components: {},
        data() {
            return {
                custPayItem: null,
                custItem: null,
                selected: '2',
            }
        },
        mounted() {
            const custId = this.$route.params.custId;
            const payId = this.$route.params.payId;

            axios.get('silver/cust/unpaid/'+custId)
                .then(res => {
                    this.custPayItem = res.data.custPay;
                    console.log(res.data.custPay);

                    this.custItem = res.data.cust;
                    console.log(res.data.cust);

                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        methods: {
            submit () {
                axios.post('/silver/cust/custPayUpdate', {
                    id: this.$route.params.payId,
                    custId: this.$route.params.custId,
                    payType: this.selected,
                    amount: this.amount,
                    userInfo: this.userInfo
                })
                .then(data => {
                    this.$router.push({name:"paymanageupdate", params: {custId: this.$route.params.custId}});
                })
                .catch(function (error) {
                    console.log(error)
                });
            }
        },
        computed: {
        }
    }
</script>
